// /pages/tags.tsx
import Link from "next/link";
import Header from "../components/Header";
import { getAllPosts, Post } from "../lib/posts";
import tagMap from "../lib/tagMap";

interface TagsPageProps {
  tags: { name: string; count: number }[];
}

export default function TagsPage({ tags }: TagsPageProps) {
  return (
    <div className="container" style={{ maxWidth: "640px", margin: "0 auto" }}>
      <Header buttons={[{ label: "Posts", href: "/posts" }]} />

      <main style={{ marginTop: "2rem" }}>
        <h1>All Tags</h1>

        <div
          style={{
            position: "relative",
            width: "100%",
            height: "400px",
            marginTop: "1rem",
            backgroundImage: "url('/images/tags-heatmap.png')",
            backgroundSize: "cover",
            backgroundPosition: "center",
            borderRadius: "10px",
            overflow: "hidden",
          }}
        >
          {tags.map((tag) => {
            // 随机位置
            const top = 5 + Math.random() * 90; // 百分比
            const left = 5 + Math.random() * 90;

            // 根据 count 决定字体大小
            const fontSize = 0.8 + (tag.count / 10) * 1.5; // 0.8rem ~ 2.3rem，可调

            return (
              <Link key={tag.name} href={`/tags/${tag.name}`}>
                <div
                  style={{
                    position: "absolute",
                    top: `${top}%`,
                    left: `${left}%`,
                    padding: "0.1rem 0.3rem",
                    borderRadius: "6px",
                    backgroundColor: "rgba(255,255,255,0.7)",
                    cursor: "pointer",
                    fontSize: `${fontSize}rem`,
                    fontWeight: 500,
                    transition: "background-color 0.2s, transform 0.2s",
                    whiteSpace: "nowrap",
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor =
                      "rgba(220,220,220,0.8)";
                    e.currentTarget.style.transform = "scale(1.1)";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor =
                      "rgba(255,255,255,0.7)";
                    e.currentTarget.style.transform = "scale(1)";
                  }}
                >
                  * {tagMap[tag.name] || tag.name}
                </div>
              </Link>
            );
          })}
        </div>
      </main>
    </div>
  );
}

// 获取标签及权重
export async function getStaticProps() {
  const posts: Post[] = getAllPosts();

  const tagCountMap: Record<string, number> = {};

  posts.forEach((post) => {
    post.tags.forEach((t) => {
      tagCountMap[t] = (tagCountMap[t] || 0) + 1;
    });
  });

  const tags = Object.entries(tagCountMap).map(([name, count]) => ({
    name,
    count,
  }));

  return {
    props: { tags },
  };
}
